@font-face {
    font-family: 'EF_jejudoldam';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2210-EF@1.0/EF_jejudoldam.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'LINESeedKR-Bd';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_11-01@1.0/LINESeedKR-Bd.woff2') format('woff2');
    font-weight: 700;
    font-style: normal;
}

div.background {
    min-height: calc(var(--vh, 1vh) * 100);
    min-width: 320px;
    max-width: 400px;
    margin: 0px auto;
    
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

div.headTitle {
    font-family : 'EF_jejudoldam';
    color : white;
    font-size : 3rem;

    display: flex;
    flex-direction: column;
    padding-top: 1rem;
}

div.rouletteOutline {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 3rem;
    
}
div.buttonGroup {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
button.trigger {
    border: none;
    background: none;   
}
div.update {
    display: flex;
    align-self: center;

    font-family: 'LINESeedKR-Bd';
    color : #E1BEE7;
    font-size : 1rem;
}
img.rouletteImg {
    width: 10rem;
}
div.rouletteTextOutline {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem;
    gap: 0.5rem;   
    width: 400px;
}
div.phrase {
    font-family: 'LINESeedKR-Bd';
    color : white;
    font-size : 2rem;
}
div.output {
    width: 100%;
    height: 4rem;
    background-color: #7C4DFF;
    border-radius: 1rem;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-family: 'LINESeedKR-Bd';
    color : white;
    font-size : 1.5rem;
}
a.href {
    text-decoration-line:none;
    color: white;
}